<template>
  <div>
    <BMainTopComponent />
    <div
      id="projectMonitoring"
      v-loading="loading"
      element-loading-text="拼命加载中，请稍后..."
      element-loading-spinner="el-icon-loading"
      element-loading-background="#9f9f9f91"
    >
      <el-row class="project-select">
        <!-- 选择项目 -->
        <project-select />
      </el-row>
      <el-row class="project-monitoring">
        <el-row type="flex" class="title content-title" align="middle"> 并机监控 </el-row>
        <!-- 项目数据 -->
        <el-row class="project-data">
          <el-row class="title">项目电站信息</el-row>
          <el-row>
            <el-col :span="14">
              <el-row type="flex" class="card-box-head">
                <el-card class="box-card">
                  <el-row>
                    <el-col :span="4" v-for="(item, key, index) in stateObj" :key="index">
                      <el-row class="card-box-head-label2">
                        <el-col :style="{ color: item }">{{ stateNum[key] }}</el-col>
                        <el-col>{{ key }}</el-col>
                      </el-row>
                    </el-col>
                  </el-row>
                </el-card>
              </el-row>
              <el-row class="params-info-container">
                <el-row>
                  <el-col :span="6" v-for="(e, i) in paramsData" :key="i">
                    <el-row type="flex" justify="center" align="middle" class="name">
                      {{ e.name }}
                    </el-row>
                    <el-row type="flex" justify="center" align="middle" class="value">{{
                      e.value
                    }}</el-row>
                  </el-col>
                </el-row>
              </el-row>
            </el-col>
            <el-col :span="10">
              <el-row class="station-info-container">
                <el-row class="top">
                  <el-row class="a-row" type="flex" align="middle">
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon icon-class="project-monitoring-workmode"></svg-icon>
                        <div class="name">工作模式：</div>
                        <div class="value">模式1</div>
                      </div>
                    </el-col>
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon icon-class="project-monitoring-worktime"></svg-icon>
                        <div class="name">工作时间：</div>
                        <div class="value">15:41:35</div>
                      </div>
                    </el-col>
                  </el-row>
                  <el-row class="a-row" type="flex" align="middle">
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon bigger icon-class="project-monitoring-temperature"></svg-icon>
                        <div class="name">环境温度：</div>
                        <div class="value">26.4℃</div>
                      </div>
                    </el-col>
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon smaller icon-class="project-monitoring-height"></svg-icon>
                        <div class="name">海拔高度：</div>
                        <div class="value">888m</div>
                      </div>
                    </el-col>
                  </el-row>
                  <el-row class="a-row" type="flex" align="middle">
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon bigger icon-class="project-monitoring-curve"></svg-icon>
                        <div class="name">谐波含量：</div>
                        <div class="value">3.2%</div>
                      </div>
                    </el-col>
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon icon-class="project-monitoring-cos"></svg-icon>
                        <div class="name">功率因数：</div>
                        <div class="value">cosδ=0.987</div>
                      </div>
                    </el-col>
                  </el-row>
                  <el-row class="a-row" type="flex" align="middle">
                    <el-col :span="12">
                      <div class="box">
                        <svg-icon icon-class="project-monitoring-three"></svg-icon>
                        <div class="name">三相不平衡：</div>
                        <div class="value">xxx</div>
                      </div>
                    </el-col>
                  </el-row>
                </el-row>
                <el-row class="bottom">
                  <el-row>
                    <div class="box">
                      <div class="name">今日运行时长</div>
                      <div class="progress">
                        <el-progress
                          color="##cbf987"
                          :stroke-width="8"
                          :percentage="50"
                          :show-text="false"
                        ></el-progress>
                      </div>
                      <div class="text">15:25:31</div>
                    </div>
                  </el-row>
                  <el-row>
                    <div class="box">
                      <div class="name">低负荷运行时间</div>
                      <div class="progress">
                        <el-progress
                          color="#aaaaaa"
                          :stroke-width="8"
                          :percentage="20"
                          :show-text="false"
                        ></el-progress>
                      </div>
                      <div class="text">15:25:31</div>
                    </div>
                  </el-row>
                  <el-row>
                    <div class="box">
                      <div class="name">高负荷运行时间</div>
                      <div class="progress">
                        <el-progress
                          color="#ea7f8e"
                          :stroke-width="8"
                          :percentage="33"
                          :show-text="false"
                        ></el-progress>
                      </div>
                      <div class="text">15:25:31</div>
                    </div>
                  </el-row>
                </el-row>
              </el-row>
            </el-col>
          </el-row>
        </el-row>
        <!-- 3.机组运行数据 -->
        <el-row class="operating-data">
          <el-row class="title">机组运行数据</el-row>
          <!-- 3.1.选择机组 -->
          <el-row class="mch-select" type="flex" align="middle">
            <mch-select @searchHandler="searchHandler" @checkDetailHandler="checkDetailHandler" />
          </el-row>
          <!-- 3.2.机组信息列表 -->
          <el-row class="mch-info-table">
            <!-- <el-checkbox v-model="checked" :disabled="disabled">展示并机组分机搜索结果</el-checkbox> -->
            <transverse-table :mchData="mchDataAll" />
          </el-row>
          <!-- 3.3.跳转到项目详细数据 -->
          <!-- <el-row class="to-project-detail" type="flex" justify="end">
            <el-button type="text" @click="checkDetailHandler">>>查看详细数据</el-button>
          </el-row> -->
        </el-row>
      </el-row>
    </div>
  </div>
</template>

<script>
import ProjectSelect from '@/components/ProjectSelect'
import MchSelect from './ToggleCrewNoList'
import TransverseTable from '@/components/TransverseTable'

export default {
  name: 'projectMonitor',
  components: {
    ProjectSelect,
    MchSelect,
    TransverseTable
  },
  data() {
    return {
      // 机组状态的颜色
      stateObj: {
        报警: '#f1534f',
        警告: '#f2c614',
        运行: '#20be62',
        停机: '#f1534f',
        离线: '#808080',
        全部: '#000000'
      },
      // 机组状态信息
      stateNum: {
        报警: 0,
        警告: 1,
        运行: 2,
        停机: 3,
        离线: 4,
        全部: 10
      },
      // 报警数据（第四个卡片 table 数据）
      tableData: [
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        },
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        },
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机发电A相电压低停机发电A相电压低停机发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        },
        {
          mchCode: 'FCZZL-21001',
          content: '发电A相电压低停机',
          time: '2022-1-12 11:22:01'
        }
      ],
      // 机组数据
      mchData: [
        {
          code: 'FPSS93476638-09',
          data: {
            level: '1',
            state: '可用',
            location: '成都市武侯区武汉路',
            maxRate: '550Kw',
            limitRate: '200Kw',
            useState: '投入',
            lifetime: '磨合期（92%）',
            averageNormalTime: '120h',
            loadNumber: 2
          }
        },
        {
          code: 'FPSS93476638-09',
          data: {
            level: '1',
            state: '可用',
            location: '成都市武侯区武汉路',
            maxRate: '550Kw',
            limitRate: '200Kw',
            useState: '投入',
            lifetime: '磨合期（92%）',
            averageNormalTime: '120h',
            loadNumber: 2
          }
        },
        {
          code: 'FPSS93476638-09',
          data: {
            level: '1',
            state: '可用',
            location: '成都市武侯区武汉路',
            maxRate: '550Kw',
            limitRate: '200Kw',
            useState: '投入',
            lifetime: '磨合期（92%）',
            averageNormalTime: '120h',
            loadNumber: 2
          }
        }
      ],
      // 展示并机组分机单选框状态
      checked: false,
      // 遮罩
      loading: false,
      // 参数数据
      paramsData: [
        {
          name: '项目总功率',
          value: '220kw'
        },
        {
          name: '实际投入功率',
          value: '180kw'
        },
        {
          name: '项目总负荷率',
          value: ''
        },
        {
          name: '实际总负荷率',
          value: ''
        },
        {
          name: '项目总运行时间',
          value: ''
        },
        {
          name: '项目总发电量',
          value: ''
        },
        {
          name: '项目总燃油用量',
          value: ''
        },
        {
          name: '平均油耗',
          value: ''
        },
        {
          name: '项目平均负荷率',
          value: ''
        },
        {
          name: '总有功峰值负荷率',
          value: ''
        },
        {
          name: '总无功峰值负荷率',
          value: ''
        },
        // 单元格补齐
        {
          name: '',
          value: ''
        }
      ]
    }
  },
  computed: {
    mchDataAll() {
      const mchDataAll = []
      this.mchData.forEach((item) => {
        mchDataAll.push(item)
        if (item.hasSub === true && this.checked === true) {
          item.subMch.forEach((e) => {
            mchDataAll.push(e)
          })
        }
      })
      return mchDataAll
    },
    // 展示并机组分机单选框是否可选，若无并机组则不可选择
    disabled() {
      let flag = true
      this.mchData.map((item) => {
        if (item.hasSub) flag = false
      })
      return flag
    }
  },
  methods: {
    // 故障诊断点击事件
    operatingHandler(scope) {
      this.$message(`点击了故障诊断，机组编码为：` + scope.mchCode)
    },
    // 查看详细数据按钮点击事件
    checkDetailHandler() {
      // this.$message(`点击了查看详细数据`)
      this.$router.push('/project/project-monitor-detail')
    },
    // 搜索机组点击事件
    searchHandler(data) {
      this.$message(`点击了搜索`)
    }
    // 向服务器请求机组数据（根据筛选条件）
  }
}
</script>

<style lang="less" scoped>
// 标题前图标的颜色
@color-title: #1abc9c;
// 淡灰色字体
@font-style-gray: #999999;
// 中部右侧字体大小
@middle-right-font: 1.2rem;
// 统一边距
.padding {
  padding: 0 0 0 15px;
}
// 大数字
.bigNum {
  font-size: 1.8rem;
}
// 卡片中的大文字
.bigFont {
  font-size: 1.2rem;
}
#projectMonitoring {
  margin-bottom: 24px;
  // 1.选择项目
  // 2.项目数据
  .project-monitoring {
    // 小标题样式
    .title {
      .padding();
      margin: 10px 0;
      &::before {
        left: 0;
        content: '';
        width: 10px;
        height: 10px;
        position: absolute;
        border-radius: 10px;
        background: @color-title;
        transform: translateY(50%);
      }
    }
    > .title {
      .padding();
      height: 45px;
      margin: 10px 0 0 0;
      background: #f3f3f3;
      // border: 1px solid #e4e4e4;
    }
    .project-data {
      .card-box-head {
        height: 110px;
        .box-card {
          width: 100%;
        }
        // 卡片间隔
        > .el-col {
          padding: 10px;
          .el-card {
            height: 100px;
            .el-col {
              display: flex;
              justify-content: center;
            }
          }
        }
        &-label {
          .el-col {
            &:nth-child(1) {
              .bigFont();
              color: @font-style-gray;
            }
            &:nth-child(2) {
              .bigNum();
              margin: 10px 0;
            }
          }
        }
        &-label2 {
          .el-col {
            &:nth-child(1) {
              .bigNum();
              color: #999999;
            }
            &:nth-child(2) {
              .bigFont();
              margin: 10px 0;
              color: @font-style-gray;
            }
          }
        }
      }
      .params-info-container {
        height: 200px;
        margin: 20px 0 0 0;
        border-left: 1px solid #e7e7e7;
        border-top: 1px solid #e7e7e7;
        > .el-row {
          height: percentage(0.3333);
          > .el-col {
            height: 100%;
            > .el-row {
              height: 50%;
              border-right: 1px solid #e7e7e7;
              border-bottom: 1px solid #e7e7e7;
              &:first-child {
                font-weight: bold;
              }
            }
          }
        }
      }
      .station-info-container {
        margin: 0 0 0 20px;
        border: 1px solid #e7e7e7;
        height: calc(200px + 20px + 110px);
        .top .box {
          display: flex;
          align-items: center;
          padding: 10px 0 0 20px;
          svg {
            color: #27bc9c;
            font-size: 2rem;
            // 确保最后的文字能对齐
            width: 10%;
            &[smaller] {
              font-size: 2.1rem;
              padding-right: 0.1rem;
            }
            &[bigger] {
              font-size: 2.4rem;
            }
          }
          .name {
            width: 45%;
            font-size: @middle-right-font;
            transform: translateX(10px);
          }
          .value {
            font-size: @middle-right-font;
          }
        }
        .top > .el-row:first-child .box {
          padding-top: 20px;
        }
        .bottom .box {
          display: flex;
          font-size: @middle-right-font;
          align-items: center;
          padding: 20px 0 0 20px;
          .name {
            width: 30%;
          }
          .progress {
            width: 35%;
            margin: 0 5px 0 0px;
          }
        }
      }
      .card-box-bottom {
        > .el-col .el-card {
          height: 120px;
        }
        &-label {
          > .el-col {
            &:nth-child(1) {
              .bigFont();
              color: #999999;
            }
            &:nth-child(2) {
              .bigNum();
              margin: 10px 0;
            }
            &:nth-child(3) {
              color: @font-style-gray;
            }
          }
        }
        > div:last-child .el-card {
          /deep/ .el-card__body {
            padding: 0;
          }
          /deep/ .el-table .cell {
            min-height: 20px;
            line-height: 20px;
          }
          // 变更滚动条样式
          /deep/::-webkit-scrollbar-thumb {
            background-color: #a4c2fe;
          }
        }
      }
    }
  }
  // 3.机组运行数据
  .operating-data {
    .mch-info-table {
      padding: 15px 0 0 0;
    }
  }
}
</style>



